body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: darkcyan;
}
.boardContainer {
  width: 400px;
  /* border: 2px red solid; */
  margin:auto;
  margin-top: 50px;

}
.board {
  display: inline-flex;
  flex-wrap: wrap;
}
.square {
  width:100px;
  height: 100px;
  border: 1px black solid;
  line-height: 80px;
  text-align: center;
  display: inline-block;
  background-color: darkslateblue;
  font-size: 50px;
}
.square:hover {
  background-color:brown;
}
.welcomeWrapper {
  text-align: center;
  position: relative;
}
button {
  width:250px;
  padding: 10px;
  margin:auto;
  cursor: pointer;
  background-color: #013243;
  color: white;
  font-family: 'Courier New', Courier, monospace;
  font-size: 18px;
  font-style: oblique;
  fill-opacity: inherit;
  border-color: blueviolet;
}
button:hover {
  background-color: green;
}
.inputSection {
  font-family: 'Courier New', Courier, monospace;
  font-size: 20px;
  font-weight: 200;
}
label input{
   max-width:300px;
   height:auto;
   padding:10px;
   border: 3px red solid;
   font-family: 'Courier New', Courier, monospace;
  font-size: 18px;
  font-style: normal;
  font-style: initial;
  /* fill-opacity: inherit; */
  border-color: blueviolet;
}
.submit {
  width:150px;
  height:auto;
  padding:10px;
  font-family: 'Courier New', Courier, monospace;
  font-size: 20px;
  font-style: oblique;
  /* border: 3px red solid; */
  background-color: #013243;
  color:rgb(234, 241, 241);
  border-color: blueviolet;
}
.submit:hover {
  background-color: green;
}
.nameShow {
  font-family: 'Courier New', Courier, monospace;
  font-size: 20px;
  font-style: normal;
  font-style: initial;
}
/* footer part */
.footer {
  background-color: dimgray;
  left: 0;
  bottom: 0;
  position: relative;
  color:white;
  text-align: center;
  padding: 5px;
  opacity: 0.4;
  width: 100%;
  height: 40px; 
}
a {
  color: black;
  text-decoration: none;
}
